import { computed, defineComponent, ref, watch } from "vue";
import { formatSongTime } from "@/utils/formatSongTime";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { mapMusicInfo } from "@/hook/mapMusicInfo";
import loading from "@/components/loading.vue";
export default defineComponent({
  name: "",
  components: {},
  props: {
    // 是否显示操作
    isHandleShow: {
      type: Boolean,
      default: true
    },
    // 是否显示头部
    showHeader: {
      type: Boolean,
      default: true
    },
    tabStyle: {
      type: Object,
      default: () => {
        return {
          fontSize: "13px",
          height: "40px"
        };
      }
    },
    // 歌曲列表
    songList: {
      type: Array,
      required: true
    },
    // 是否显示专辑项
    // isAlbumShow: {
    //   type: Boolean,
    //   default: true,
    // },
    // 是否显示下标项
    isIndexShow: {
      type: Boolean,
      default: true
    },
    // 是否显示图片
    showImg: {
      type: Boolean,
      default: false
    },
    propList: {
      type: Array,
      default: () => {
        return [{
          prop: "author",
          label: "歌手",
          slotName: "author"
        }, {
          prop: "album",
          label: "专辑",
          "min-width": "50",
          slotName: "album"
        }, {
          prop: "time",
          label: "时间",
          align: "right",
          "header-align": "right"
        }];
      }
    }
  },
  emits: ["onhandleClick"],

  setup(props, {
    emit
  }) {
    const store = useStore();
    const route = useRoute(); // 弹窗控制

    const centerDialogVisible = ref(false); // 当前列表是否存在正在播放的音乐

    const isCurrentPlayMusic = ref(false); // 播放图标

    const img = require("../assets/image/activity-play.png"); // 映射歌曲信息


    const songlist = computed(() => {
      if (props.songList) {
        return mapMusicInfo(props.songList);
      }
    }); // 监听用户点击歌曲列表

    const changeClick = (row, column) => {
      // 歌曲是否可用
      // store.dispatch("playMusic/isMusicPlay", row.id).then((res) => {
      //   if (!res.success) {
      //     centerDialogVisible.value = true;
      //     return;
      //   }
      // });
      // 将点击的歌曲和当前歌曲列表发送出去
      emit("onhandleClick", row, props.songList);
    }; // 当前音乐列表的下标


    const songlistindex = ref(); // 当前歌曲列表

    const songlistcurrent = ref();
    /**突出正在播放的音乐
     * @param {number} currentSongList 当前歌曲列表
     * @param {Object} currentSong 当前播放歌曲
     */

    const actionMusic = (currentSongList, currentSong) => {
      const index = currentSongList.findIndex(item => item.id === currentSong); // 拿到查找歌曲的下标

      if (index !== -1) {
        isCurrentPlayMusic.value = true;
        songlistindex.value = index + 1;
      } else {
        isCurrentPlayMusic.value = false;
      }
    };

    watch( //监听播放歌曲信息的变化
    () => songlist.value, value => {
      songlistcurrent.value = value; // 查找当前歌曲列表是否存储播放的歌曲

      actionMusic(value, store.state.playMusic.musicInfo.id);
    }, {
      immediate: true
    }); // 监听歌曲切换

    watch(() => store.state.playMusic.musicInfo, value => {
      actionMusic(songlistcurrent.value, value.id);
    }); // 导入歌曲喜欢hook

    const {
      likelist,
      uselike
    } = require("@/hook/useLike.js"); // 喜欢此歌曲


    const loveClick = id => {
      uselike(id);
    }; // 取消喜欢的歌曲


    const dislike = id => {
      uselike(id, false);
    };

    return {
      changeClick,
      songlist,
      formatSongTime,
      img,
      centerDialogVisible,
      songlistindex,
      isCurrentPlayMusic,
      songlistcurrent,
      likelist,
      loveClick,
      dislike
    };
  }

});